//سرچ محصولات
let [searchvalue,setsearchvalue]=useState('')
let [filstatus,setfilstatus]=useState(1)
function search() {
  

    let filter=allorder.filter((i)=>i.barcode.toString().includes(searchvalue)||i.namee.includes(searchvalue))
    if(filstatus===1){
        let or=[...filter]
    
        let oo=or.sort((a,b)=>{
            let valueA =new Date(a.datey).getTime() 
          let valueB = new Date(b.datey).getTime() 
          return  valueA-valueB ;
        })
      console.log(oo);
      setorder(oo)  }
    else if(filstatus===2){

        let or=[...filter]
    
        let oo=or.sort((a,b)=>{
            let valueA =new Date(a.datey).getTime() 
          let valueB = new Date(b.datey).getTime() 
          return  valueB-valueA ;
        })
      console.log(oo);
      setorder(oo)  
    }
    else if(filstatus===3){
    
        let or=[...filter]
    
        let oo=or.sort((a,b)=>{
            let valueA = a.numbers
          let valueB = b.numbers
          return  valueA-valueB ;
        })
        console.log(oo);
      setorder(oo)}
    else if(filstatus===4){
   
        let or=[...filter]
    
        let oo=or.sort((a,b)=>{
            let valueA = a.numbers
          let valueB = b.numbers
          return  valueB-valueA ;
        })
        console.log(oo);
      setorder(oo)  
    }
    else if(filstatus===5){

        let or=[...filter]
        let oo=or.sort((a,b)=>{
            let valueA = parseInt(a.price.replace(/,/g, ''));
          let valueB = parseInt(b.price.replace(/,/g, ''));
          return  valueA-valueB ;
        })
        console.log(oo);
        setorder(oo)  
    }
    else if(filstatus===6){    
    let or=[...filter]
    let oo=or.sort((a,b)=>{
        let valueA = parseInt(a.price.replace(/,/g, ''));
      let valueB = parseInt(b.price.replace(/,/g, ''));
      return  valueB-valueA ;
    })
    console.log(oo);
    setorder(oo)  
    
    }}
